.menu-toggle {
  position: relative;

  width: 34px;
  height: 30px;
  margin-bottom: 10px;
  padding: 0;

  color: transparent;
  background-color: transparent;
  border: none;

  cursor: pointer;
}

.menu-toggle span {
  position: relative;
  z-index: 1;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 3px;

  font-size: 0;
  color: transparent;
  background: #000000;

  cursor: pointer;
  transition: 0.1s 0.1s;
  border-radius: 3px;
}

.menu-toggle.active span {
  background-color: transparent;
}

.menu-toggle::before,
.menu-toggle::after {
  content: "";
  position: absolute;
  right: 0px;
  width: 100%;
  height: 3px;

  background-color: #000000;
  border-radius: 3px;
  transition: 0.3s 0.3s, transform 0.3s;
}

.menu-toggle::before {
  top: 0;
}

.menu-toggle::after {
  bottom: 0;
}

.active.menu-toggle::before {
  transform: rotate(-45deg);
  transition: 0.3s, transform 0.3s 0.3s;
  top: 14px;
}

.active.menu-toggle::after {
  transform: rotate( 45deg);
  transition: 0.3s, transform 0.3s 0.3s;
  bottom: 14px;
}
