.main-swiper {
  counter-reset: mainSwiperCurrent;
}

.main-swiper__subtitle-wrap {
  margin-left: 118px;
  margin-bottom: 26px;
}

.main-swiper__pagination {
  top: 3px;
  bottom: @a;
  left: 50%;
  transform: translateX(-722px);

  font-weight: 700;
  font-size: 18px;

  &.swiper-pagination {
    width: 80px;
  }
}

.main-swiper__subtitle {
  &::after {
    right: -374px;
    width: 367px;
  }
}

.main-swiper__text-wrap {
  position: relative;
  z-index: 1;
  max-width: 1100px;
  min-height: 154px;
  margin-left: 118px;
  margin-bottom: 40px;
}

.main-swiper__title {
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 45px;
  line-height: 50px;
}

.main-swiper__desc {
  font-size: 18px;
  line-height: 27px;
}

.main-swiper__img-block {
  position: relative;
  max-width: 1920px;
  height: 523px;

  .margin-inline(@a);

  &::before {
    counter-increment: mainSwiperCurrent;
    content: counter(mainSwiperCurrent, decimal-leading-zero);

    position: absolute;
    top: -153px;
    left: 40px;

    font-weight: 800;
    font-size: 302px;

    color: transparent;

    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #fff;
  }
}

.main-swiper__img {
  .img-cover;
}

.main-swiper__arrows-wrap {
  top: 95px;
  // right: 255px;
  right: 50%;
  transform: translateX(705px);
}

@media (max-width: 1920px) {
  .main-swiper__arrows-wrap {
    right: 255px;
    transform: none;
  }
}

@media (max-width: 1810px) {
  .main-swiper__arrows-wrap {
    right: 80px;
  }
}

@media (max-width: 1450px) {
  .main-swiper__pagination {
    transform: translateX(-@d956px);
  }
  .main-swiper__arrows-wrap {
    top: 50px;
    right: @a;
    left: 0;
  }

  .main-swiper__img-block {
    &::before {
      top: -90px;
      left: 10px;
      font-size: 130px;
    }
  }
}

@media (max-width: 750px) {
  .main-swiper {
    margin-top: 0;
  }

  .main-swiper__pagination {
    display: none;
  }

  .main-swiper__subtitle-wrap {
    position: relative;
    margin-bottom: 0px;
    top: 40px;
    max-width: 260px;
    margin-left: 0;
  }

  .main-swiper__subtitle {
    &::after {
      bottom: 9px;
      right: -159px;
      height: 3px;
      width: 135px;
    }
  }

  .main-swiper__arrows-wrap {
    width: 124px;
    top: -3.5px;
    right: 18px;
    left: @a;
  }

  .main-swiper__button-next,
  .main-swiper__button-prev {
    width: 42px;
  }

  .main-swiper__container {
    padding-top: 80px;
  }

  .main-swiper__text-wrap {
    min-height: 0px;
    margin-left: 0;
    margin-bottom: 38px;
  }

  .main-swiper__title {
    margin-bottom: 0px;
    font-size: 30px;
    line-height: 35px;
  }

  .main-swiper__img-block {
    height: 212px;
    margin-right: 28px;
    margin-bottom: 45px;

    &::before {
      content: none;
    }
  }

  .main-swiper__desc {
    position: relative;
    max-width: 240px;
    margin-left: 134px;
    margin-bottom: 20px;

    &::before {
      counter-increment: mainSwiperCurrent;
      content: counter(mainSwiperCurrent, decimal-leading-zero);

      position: absolute;
      top: 19px;
      left: -120px;

      font-weight: 800;
      font-size: 100px;

      color: transparent;

      -webkit-text-stroke-width: 2px;
      -webkit-text-stroke-color: #1c1818;
    }
  }
}